<template>
  <div>
		<transition name="slide-fade">
			<div class="page" v-if="isShow" @click="hide"></div>
		</transition>
		<button @click="show">click</button>
	</div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false
    };
  },
  mounted() {},
  methods: {
    show() {
      this.isShow = true;
    },
    hide() {
      this.isShow = false;
    }
  }
};
</script>

<style>
.slide-fade-enter-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter {
  transform: translate3d(100%, 0, 0);
}
.slide-fade-leave-to {
  transform: translate3d(-100%, 0, 0);
}
.page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #333;
}
</style>



