<template>
  <div class="scrolllinkage-container">
      <div class="header">滚动联动监听</div>
      <div class="content">
          <div class="left-wrap" ref="leftWarp">
              <div>
                  <div class="left-item" v-for="(item,index) in leftItemList" ref="leftItemList">{{index+1}}-{{item}}</div>
              </div>
          </div>
          <div class="right-wrap" ref="rightWarp">
              <div>
                  <div class="right-item" v-for="(item,index) in rightItemList" ref="rightItemList">
                      <div class="title">{{item.name}}</div>
                      <div class="detail">
                          <div class="detail-content" v-for="(item,index) in item.detail">{{item}}</div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import Bscroll from "better-scroll";
export default {
  data() {
    return {
      leftItemList: [
        "左1",
        "左2",
        "左3",
        "左4",
        "左5",
        "左6",
        "左7",
        "左8",
        "左9",
        "左10"
      ],
      rightItemList: [
        { name: "右1", detail: ["右1-1", "右1-2", "右1-3", "右1-4", "右1-5"] },
        {
          name: "右2",
          detail: [
            "右2-1",
            "右2-2",
            "右2-3",
            "右2-4",
            "右2-5",
            "右2-6",
            "右2-7",
            "右2-8"
          ]
        },
        { name: "右3", detail: ["右3-1", "右3-2", "右3-3", "右3-4", "右3-5"] },
        { name: "右4", detail: [] },
        { name: "右5", detail: [] },
        { name: "右6", detail: [] },
        { name: "右7", detail: [] },
        { name: "右8", detail: [] }
      ],
      listHeight: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.leftWarp, {
        click: true //better-scroll 默认会阻止浏览器的原生 click 事件
      });
      this.scroll = new Bscroll(this.$refs.rightWarp, {
        click: true //better-scroll 默认会阻止浏览器的原生 click 事件
      });
    });
    this._calculateHeight();
  },
  methods: {
    _calculateHeight() {
      let rightItemList = this.$refs.rightItemList;
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < rightItemList.length; i++) {
        let item = rightItemList[i];
        height += item.clientHeight;
        this.rightItemList.push(height);
      }
      console.log(this.rightItemList);
    }
  }
};
</script>

<style>

</style>



